@import url(https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@400;500;800&family=Lato&display=swap);
body {
  margin: 0;
  font-family: 'Darker Grotesque', -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, Ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/UserDirectory/static/media/dir.9f0b20ee.jpg);
  background-size: cover;
}

/* Typography */
h1 {
  font-size: 56px;
  font-weight: 400;
}

h2 {
  font-size: 28px;
  font-weight: 500;
  margin-top: 20px;
  text-transform: capitalize;
}

h3 {
  font-size: 16px;
  font-weight: 800;
  letter-spacing: 2px;
  text-transform: uppercase;
}

p, 
a {
  font-family: 'lato', -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, Ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

a:hover {
  text-decoration: none;
  color: rgb(241, 174, 129);
  transition: ease .5s;
}

/* Colors */
.text-color {
  color: rgb(0, 0, 0);
}
.interactive-color {
  color: rgb(0, 0, 0);
}

/* —————————————————————————————————————————————
MEDIA QUERIES 
————————————————————————————————————————————— */
@media only screen and (max-width: 768px) {
  h1 {
    font-size: 46px;
  }
}
.header {
    background-color: #fff;
    vertical-align: middle;
 }
 
@media only screen and (max-width: 768px) {
    .button-group {
      display: flex;
      justify-content: center;
    }
  }

.margin {
    margin-bottom: 20px;
  }
  
  .form-control {
    padding: 22px 0 22px 2.5rem;
    margin-top: 20px;
  }
  
  .has-search .form-control-feedback {
    position: absolute;
    z-index: 2;
    display: block;
    top: 16px;
    left: 32px;
    font-size: 14px;
    text-align: center;
    pointer-events: none;
    color: rgb(0, 0, 0);
    margin-top: 20px;
  }
  
  .search-input {
    font-family: 'lato', -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, Ubuntu, roboto, noto, segoe ui, arial, sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: rgb(0, 0, 0);
    background-color: #ffffff;
    border: none;
  }
  
  .search-input::-webkit-input-placeholder {
    color: rgba(0, 0, 0, 0.7);
  }
  
  .search-input:-ms-input-placeholder {
    color: rgba(0, 0, 0, 0.7);
  }
  
  .search-input::-ms-input-placeholder {
    color: rgba(0, 0, 0, 0.7);
  }
  
  .search-input::placeholder {
    color: rgba(0, 0, 0, 0.7);
  }
  
  .search-input {
    border-radius: 50px;
  }
  
  .search-input:focus  {
    background-color: #ffffff;
    color: rgb(0, 0, 0);
  }
  
  
.dropdown, 
.btn {
  font-family: 'lato', -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, Ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: rgb(0, 0, 0);
  background-color: #ffffff;
  border: none;
  margin-top: 20px;
}

.btn:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(3, 3, 3);
}
.btn:focus {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
}

.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show>.btn-primary.dropdown-toggle:focus {
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show>.btn-primary.dropdown-toggle:active {
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
}

.margin {
  margin-bottom: 20px;
}

.dropdown-menu {
  background-color: #ffffff;
  transition: ease .2s;
}

.dropdown-item {
  color: rgb(0, 0, 0);
}

.dropdown-item:hover {
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}






@media only screen and (max-width: 768px) {
  

  .full-width {
    width: 100%;
  }

}
.wrapper {
    padding-top: 25px;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
.card-container {
    background: #ffffff;
    border-radius: 18px;
    box-shadow: 0px 4px 20px 0px rgba(0,0,0,0.15);
    margin-bottom: 20px;
    width: 265px;
  }
  
  .card-intro {
    text-align: center;
    padding: 50px 10px 20px;
  }
  
  hr {
    border-top: 3px solid rgba(0,0,0,.1);
  }
  
  
  .card-contact {
    padding: 15px 0 20px;
  }
  
  li {
    color: rgb(0, 0, 0);
    list-style: none;
    margin-bottom: 15px;
  }
  
  a {
    padding-left: 10px;
    color: rgb(0, 0, 0);
  }
